@charset "UTF-8";
.page {
  height: 100%;
  font-family: "PT Sans", sans-serif;
  color: #333333;
  background-color: #f2f2f2;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
}

.page__body {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-color: white;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}
@media screen and (min-width: 768px) {
  .page__body {
    margin: 0 auto;
  }
}
@media screen and (min-width: 1200px) {
  .page__body {
    width: 1200px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.advantages-list {
  position: relative;
  counter-reset: advantages;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media screen and (min-width: 768px) {
  .advantages-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.advantages-list__item--advanced {
  grid-column: 1/span 3;
}

.advantages-item {
  padding: 0 18px;
  background-color: #eeeeee;
  box-sizing: border-box;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .advantages-item {
    width: 100%;
    padding: 0;
  }
}

.advantages-item__content-wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 24px 18px;
  text-align: center;
  border-bottom: 1px solid #d6d6d6;
}
@media screen and (min-width: 768px) {
  .advantages-item__content-wrapper {
    justify-content: flex-start;
    align-items: center;
    border: 0;
    padding: 40px 20px;
    max-width: 228px;
    min-height: 256px;
    box-sizing: border-box;
  }
  .advantages-item__content-wrapper--road {
    max-width: 190px;
  }
}
@media screen and (min-width: 1200px) {
  .advantages-item__content-wrapper {
    max-width: 350px;
    padding: 42px 20px;
  }
}

.advantages-item__title {
  margin: 0;
  margin-bottom: 17px;
  color: black;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.advantages-item__title::after {
  margin-top: 12px;
  display: block;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  counter-increment: advantages;
  content: "— №" counter(advantages) " —";
}
@media screen and (min-width: 768px) {
  .advantages-item__title {
    margin-bottom: 24px;
    max-width: 155px;
    font-size: 21px;
    line-height: 26px;
    font-weight: 700;
  }
}
@media screen and (min-width: 1200px) {
  .advantages-item__title {
    margin-bottom: 25px;
    max-width: 155px;
  }
}
.advantages-item__title--square {
  margin-top: 2px;
}
@media screen and (min-width: 768px) {
  .advantages-item__title--square {
    margin-top: 0;
  }
}

.advantages-item__description {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
}
@media screen and (min-width: 768px) {
  .advantages-item__description .nowrap {
    white-space: nowrap;
  }
  .advantages-item__description--few .br {
    display: block;
  }
  .advantages-item__description--road .br {
    display: block;
  }
}
@media screen and (min-width: 1200px) {
  .advantages-item__description--few .br {
    display: inline;
  }
  .advantages-item__description--road {
    margin-left: 5px;
