.advantages-item-advanced {
  background-color: #81b3d2;
  color: white;
  text-transform: uppercase;
  border: 0;
  box-sizing: border-box;
}
.advantages-item-advanced:first-child::before {
  position: absolute;
  top: -1px;
  display: block;
  width: 100%;
  height: 37px;
  background-image: url("/img/top-triangle.svg");
