.checkbox-field {
  position: relative;
  display: block;
}
.checkbox-field:hover .checkbox-field__mark {
  background-color: #e5e5e5;
}
.checkbox-field:active .checkbox-field__mark {
  background-color: #d5d5d5;
}
.checkbox-field:active .checkbox-field__mark::after {
  opacity: 0.3;
}

.checkbox-field__label {
  color: black;
  padding-left: 51px;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}

.checkbox-field__input {
  appearance: none;
}

.checkbox-field__mark {
  position: absolute;
  top: -10px;
  left: 0;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 4px;
  z-index: 1;
  content: "";
}

.checkbox-field__input:checked + .checkbox-field__mark::after {
  position: absolute;
  width: 22px;
  height: 16px;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  background-image: url("../img/tick.svg");
  background-position: center;
  background-repeat: no-repeat;
  content: "";
}

.checkbox-field__input:focus + .checkbox-field__mark {
  background-color: #d5d5d5;
}

.text-field {
  display: block;
}

.text-field__label {
  display: block;
  margin-left: 16px;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
}

.text-field__input {
  width: 100%;
  padding: 12px;
  margin: 0;
  color: black;
  background-color: #f2f2f2;
  font-family: "PT Sans", sans-serif;
  border: 4px solid transparent;
  box-sizing: border-box;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.text-field__input:hover {
  background-color: #e5e5e5;
}
.text-field__input:focus, .text-field__input:active {
  background-color: white;
  border: 4px solid #e5e5e5;
}
.text-field__input:focus-visible {
  outline: none;
}

.text-field__input::placeholder {
  color: black;
  opacity: 0.3;
}

.text-field--phone {
  position: relative;
}
.text-field--phone::after {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 58px;
  height: 58px;
  display: block;
  background-color: #e6e6e6;
  background-image: url("../img/phone.svg");
  background-repeat: no-repeat;
  background-position: center;
  content: "";
  z-index: 2;
}
.text-field--phone .text-field__input {
  padding-right: 78px;
}

.text-field--email {
  position: relative;
}
.text-field--email::after {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 58px;
  height: 58px;
  display: block;
  background-color: #e6e6e6;
  background-image: url("../img/mail.svg");
  background-repeat: no-repeat;
  background-position: center;
  content: "";
  z-index: 2;
}
.text-field--email .text-field__input {
  padding-right: 78px;
}

