.radio-field {
  position: relative;
  display: block;
}
.radio-field:hover .radio-field__mark {
  background-color: #e5e5e5;
}
.radio-field:active .radio-field__mark {
  background-color: #d5d5d5;
}
.radio-field:active .radio-field__mark::after {
  opacity: 0.3;
}

.radio-field__label {
  padding-left: 50px;
  color: black;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}

.radio-field__input {
  appearance: none;
}

.radio-field__mark {
  position: absolute;
  top: -10px;
  left: 0;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 50%;
  z-index: 1;
  content: "";
}

.radio-field__input:checked + .radio-field__mark::after {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  transform: translateX(-50%) translateY(-50%);
  background-color: #333333;
  content: "";
}

.radio-field__input:focus + .radio-field__mark {
  background-color: #d5d5d5;
}

